import Vue from 'vue';
import i18n from '../../../i18n';
import XNotification from './deck.notification.vue';
import paging from '../../../common/scripts/paging';
import * as config from '../../../config';
const displayLimit = 20;
export default Vue.extend({
    i18n: i18n(),
    components: {
        XNotification
    },
    inject: ['column', 'isScrollTop', 'count'],
    mixins: [
        paging({
            onQueueChanged: (self, q) => {
                self.count(q.length);
            },
        }),
    ],
    props: {
        type: {
            type: String,
            required: false
        }
    },
    data() {
        return {
            connection: null,
            pagination: {
                endpoint: 'i/notifications',
                limit: 20,
                params: () => ({
                    includeTypes: this.type ? [this.type] : undefined
                })
            }
        };
    },
    computed: {
        _notifications() {
            return this.items.map(notification => {
                const date = new Date(notification.createdAt).getDate();
                const month = new Date(notification.createdAt).getMonth() + 1;
                notification._date = date;
                notification._datetext = this.$t('@.month-and-day').replace('{month}', month.toString()).replace('{day}', date.toString());
                return notification;
            });
        }
    },
    watch: {
        type() {
            this.reload();
        }
    },
    mounted() {
        this.connection = this.$root.stream.useSharedConnection('main');
        this.connection.on('notification', this.onNotification);
        this.column.$on('top', this.onTop);
        this.column.$on('bottom', this.onBottom);
    },
    beforeDestroy() {
        this.connection.dispose();
        this.column.$off('top', this.onTop);
        this.column.$off('bottom', this.onBottom);
    },
    methods: {
        onNotification(notification) {
            // TODO: ユーザーが画面を見てないと思われるとき(ブラウザやタブがアクティブじゃないなど)は送信しない
            this.$root.stream.send('readNotification', {
                id: notification.id
            });
            this.prepend(notification);
        },
        prepend(notification) {
            if (this.isScrollTop()) {
                // Prepend the notification
                this.notifications.unshift(notification);
                // オーバーフローしたら古い通知は捨てる
                if (this.notifications.length >= displayLimit) {
                    this.notifications = this.notifications.slice(0, displayLimit);
                }
            }
            else {
                this.queue.push(notification);
            }
            // サウンドを再生する
            if (this.$store.state.device.enableSounds && this.$store.state.device.enableSoundsInNotifications) {
                const sound = new Audio(`${config.url}/assets/pope2.mp3`);
                sound.volume = this.$store.state.device.soundVolume;
                sound.play();
            }
        },
        releaseQueue() {
            for (const n of this.queue) {
                this.prepend(n);
            }
            this.queue = [];
        },
        onTop() {
            this.releaseQueue();
        },
        onBottom() {
            this.fetchMoreNotifications();
        }
    }
});
